import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
} from "@/components/ui/card";
import { CalendarDays, MapPin, ArrowRight } from "lucide-react";

const events = [
  {
    title: "Open Campus Day",
    date: "March 15, 2024",
    category: "Admissions",
    description:
      "Join us for a campus tour and meet our faculty and students in an immersive open house experience.",
    location: "Main Quad",
    accent: "blue",
  },
  {
    title: "Research Symposium",
    date: "April 2, 2024",
    category: "Academic",
    description:
      "Explore groundbreaking research projects from our graduating scholars across all departments.",
    location: "Science Hall",
    accent: "purple",
  },
  {
    title: "Career Fair 2024",
    date: "April 18, 2024",
    category: "Professional",
    description:
      "Connect with leading industry employers, network with alumni, and explore internship opportunities.",
    location: "Student Center",
    accent: "emerald",
  },
];

export default function Events() {
  // Helper to format date for the UI
  const getDateParts = (dateString: string) => {
    const date = new Date(dateString);
    return {
      day: date.getDate(),
      month: date.toLocaleString("default", { month: "short" }).toUpperCase(),
      weekday: date.toLocaleString("default", { weekday: "long" }),
    };
  };

  return (
    <section className="py-24 px-6 bg-slate-50 relative overflow-hidden">
      {/* Background decoration */}
      <div className="absolute top-0 right-0 w-[500px] h-[500px] bg-blue-50 rounded-full blur-3xl -translate-y-1/2 translate-x-1/3 opacity-50 pointer-events-none" />

      <div className="max-w-7xl mx-auto relative z-10">
        {/* Section Header */}
        <div className="flex flex-col md:flex-row justify-between items-end mb-12 gap-6">
          <div>
            <h2 className="text-5xl md:text-7xl font-bold tracking-tighter text-zinc-900 leading-[0.9]">
              Upcoming Events
            </h2>
            <p className="text-lg text-slate-600 max-w-xl mt-4">
              Connect, learn, and grow. Join our vibrant community at these
              upcoming gatherings.
            </p>
          </div>
          <button className="hidden md:inline-flex items-center justify-center px-6 py-3 text-sm font-medium transition-colors bg-white border border-slate-200 rounded-full hover:bg-slate-900 hover:text-white hover:border-slate-900">
            View Full Calendar
          </button>
        </div>

        {/* Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {events.map((event, idx) => {
            const { day, month } = getDateParts(event.date);

            return (
              <Card
                key={idx}
                className="group relative border-slate-200 bg-white shadow-sm hover:shadow-xl hover:-translate-y-1 transition-all duration-300 overflow-hidden flex flex-col h-full"
              >
                {/* Top color bar based on accent */}
                <div
                  className={`h-1.5 w-full ${
                    event.accent === "blue"
                      ? "bg-blue-500"
                      : event.accent === "purple"
                      ? "bg-purple-500"
                      : "bg-emerald-500"
                  }`}
                />

                <CardHeader className="pb-0 pt-6 px-6 flex flex-row items-start justify-between space-y-0">
                  {/* Date Block */}
                  <div className="flex flex-col items-center justify-center w-16 h-16 bg-slate-50 rounded-xl border border-slate-100 group-hover:border-slate-200 transition-colors">
                    <span className="text-xs font-bold text-slate-400 uppercase tracking-wider">
                      {month}
                    </span>
                    <span className="text-2xl font-bold text-slate-900">
                      {day}
                    </span>
                  </div>

                  {/* Category Badge */}
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-slate-100 text-slate-600">
                    {event.category}
                  </span>
                </CardHeader>

                <CardContent className="pt-6 px-6 flex-grow">
                  <h3 className="text-xl font-bold text-slate-900 mb-3 group-hover:text-blue-600 transition-colors line-clamp-2">
                    {event.title}
                  </h3>

                  <div className="flex items-center gap-2 text-sm text-slate-500 mb-4">
                    <MapPin className="w-4 h-4" />
                    <span>{event.location}</span>
                  </div>

                  <p className="text-slate-600 text-sm leading-relaxed line-clamp-3">
                    {event.description}
                  </p>
                </CardContent>

                <CardFooter className="px-6 pb-6 pt-0 mt-auto">
                  <div className="w-full pt-6 border-t border-slate-100 flex items-center justify-between group-hover:border-slate-200 transition-colors">
                    <span className="text-sm font-medium text-slate-500 flex items-center gap-2">
                      <CalendarDays className="w-4 h-4" />
                      Add to Calendar
                    </span>

                    <button className="flex items-center gap-2 text-sm font-semibold text-blue-600 group/btn">
                      Details
                      <ArrowRight className="w-4 h-4 transition-transform duration-300 group-hover/btn:translate-x-1" />
                    </button>
                  </div>
                </CardFooter>
              </Card>
            );
          })}
        </div>

        {/* Mobile-only View All Button */}
        <div className="mt-8 text-center md:hidden">
          <button className="inline-flex items-center justify-center px-6 py-3 text-sm font-medium transition-colors bg-white border border-slate-200 rounded-full hover:bg-slate-100">
            View Full Calendar
          </button>
        </div>
      </div>
    </section>
  );
}
